<template>
  <div class="containerFrame">
    <div class="tabBox">
      <v-tabs v-model="tabIdx">
        <v-tab v-for="item in tabs" :key="item">
          {{ item }}
        </v-tab>
      </v-tabs>
    </div>
    <div class="fix">
      <!-- History -->
      <DetailComponent
        key="History"
        v-if="tabs[tabIdx] === 'History'"
        :obj="{
          categoryOption: {
            api: ['ship', 'alarm_history_GetSelectedShip_Combo'],
            textKey: 'ship_name',
            valueKey: 'ship_idx',
            modelKey: 'ship_idx',
          },
          disbleNewItem: true,
          detailProp: 'History Detail',
          masterApi: ['alam', 'alarm_history_alam'],
          masterName: 'History',
          masterHeaders: [
            {
              text: 'alarms_idx',
              value: 'alarms_idx',
              align: 'center',
            },
            {
              text: 'imo_id',
              value: 'imo_id',
              align: 'center',
              type: 'text',
            },
            {
              text: 'sys_type',
              value: 'sys_type',
              align: 'center',
              type: 'text',
            },
            {
              text: 'm_code',
              value: 'm_code',
              align: 'center',
              type: 'text',
            },
            {
              text: 'name',
              value: 'name',
              align: 'center',
              type: 'text',
            },
            {
              text: 'res',
              value: 'res',
              align: 'center',
              type: 'text',
            },

            {
              text: 'desc',
              value: 'desc',
              align: 'center',
              type: 'text',
            },
            {
              text: 'desc_detail',
              value: 'desc_detail',
              align: 'center',
              type: 'text',
            },
            {
              text: 'alarm_level',
              value: 'alarm_level',
              align: 'center',
              type: 'text',
            },
            {
              text: 'priority',
              value: 'priority',
              align: 'center',
              type: 'text',
            },
            {
              text: 'eas_group',
              value: 'eas_group',
              align: 'center',
              type: 'text',
            },
            {
              text: 'area',
              value: 'area',
              align: 'center',
              type: 'text',
            },
            {
              text: 'device',
              value: 'device',
              align: 'center',
              type: 'text',
            },
          ],
        }"
      />
      <!-- Current -->
      <DetailComponent
        key="Current"
        v-if="tabs[tabIdx] === 'Current'"
        :obj="{
          categoryOption: {
            api: ['ship', 'alarm_current_GetSelectedShip_Combo'],
            textKey: 'ship_name',
            valueKey: 'ship_idx',
            modelKey: 'ship_idx',
          },
          disbleNewItem: true,
          detailProp: 'Current Detail',
          masterApi: ['alam', 'alarm_current_alam'],
          masterName: 'Current',
          masterHeaders: [
            {
              text: 'alarm_status_idx',
              value: 'alarm_status_idx',
              align: 'center',
            },
            {
              text: 'imo_id',
              value: 'imo_id',
              align: 'center',
              type: 'text',
            },
            {
              text: 'instance',
              value: 'instance',
              align: 'center',
              type: 'text',
            },
            {
              text: 'alarm_time',
              value: 'alarm_time',
              align: 'center',
              type: 'text',
            },
            {
              text: 'alarm_clear_time',
              value: 'alarm_clear_time',
              align: 'center',
              type: 'text',
            },
            {
              text: 'confirm_time',
              value: 'confirm_time',
              align: 'center',
              type: 'text',
            },
            {
              text: 'alarm_status',
              value: 'alarm_status',
              align: 'center',
              type: 'text',
            },
            { text: '', value: 'icon', sortable: false },
          ],
        }"
      />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import DetailComponent from "@/components/DetailComponent.vue";
import { useMenuStore } from "@/stories/mainStore.js";

export default {
  name: "AlamView",
  components: {
    DetailComponent,
  },
  setup() {
    //

    const menuStore = useMenuStore();
    const tabIdx = ref(0);

    const tabsMap = {
      alarm_current: "Current",
      alarm_history: "History",
    };

    const tabArr = [];
    menuStore.tabs.get(location.pathname.replace(/\//, "")).map((item) => {
      if (!tabsMap[item.name]) {
        console.warn(`누락 ${item.name}`);
      } else {
        tabArr.push(tabsMap[item.name]);
      }
    });
    const tabs = ref(tabArr);

    return {
      tabs,
      tabIdx,
    };
  },
};
</script>

<style></style>
